
.mid-box {
    
  background-color: rgb(253, 244, 210);
  border-radius: 1.5em;
  border-color: rgb(218, 57, 57);
 
  text-align: center;
  padding: 0.3em;
  margin: 1em;
  min-height: 640px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  
  
}

.title {
  color: #f7cfc8;
  font-family: "Luckiest Guy", cursive;
  font-size: 40px;
  border-color: #000;
  text-shadow: 1px 1px #845f34;
  
  letter-spacing: 3px;
  text-align: center;
 
   
   
  width: 100%;
  margin-bottom: 10px;
}

.textarea{
  width: 68%;
  text-align: center;
  height: 8em;
  padding: 15px;
  border-radius: 10px;
  background-color: rgb(255, 255, 228);
  resize: vertical;
  margin-bottom: 0.1em;
  margin-left: 30px;
  margin-right: 10px;
  margin-top: 2em;
  font-size: 14px;
}

.draw-button {

  margin: auto;
  margin-top: 22px;
  margin-bottom: 15px;
  color:white;
  font-family:Helvetica, sans-serif;
  font-weight:bold;
  font-size:25px;
  text-align: center;
  text-decoration:none;
  background-color:#FFA12B;
  display:block;

  padding:0.4em;
  
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  text-shadow: 0px 1px 0px #000;
  filter: dropshadow(color=#000, offx=0px, offy=1px);
  
  -webkit-box-shadow:inset 0 1px 0 #FFE5C4, 0 5px 0 #915100;
  -moz-box-shadow:inset 0 1px 0 #FFE5C4, 0 5px 0 #915100;
  box-shadow:inset 0 1px 0 #FFE5C4, 0 5px 0 #915100;
  
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 12px;
}

.draw-button:active{
  top:10px;
  background-color:#F78900;
  
  -webkit-box-shadow:inset 0 1px 0 #FFE5C4, inset 0 -3px 0 #915100;
  -moz-box-shadow:inset 0 1px 0 #FFE5C4, inset 0 -3pxpx 0 #915100;
  box-shadow:inset 0 1px 0 #FFE5C4, inset 0 -3px 0 #915100;
}

.draw-button:after{
  content:"";

  padding:4px;
  position: absolute;
  bottom:-5px;
  left:-4px;
  z-index:-1;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}

.output-zone{
  margin-top: 10%;
  padding: 0.1em;
  
  margin-left: 25%;
  margin-right: 25%;
  border-radius: 10px;
  background-color: rgb(255, 255, 231);
  text-align: center;
   margin-bottom: 1em;

  height: 5em;
}


.animation-text{
   margin: 0;
   padding-right: 0;
   font-size: 16px;
}

.result-title{
   font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
   
}

.main-content{
    display: inline;
    align-content: space-between;
}

.h3{
  margin-left: 30%;
  margin-right: 30%;
 font-size: 16px;
  border-bottom: 1px solid rgb(187, 187, 187);
}

.clear-button{
  height:30px;
}